<template>
  <div>
    <slider class="slider" interval="3000" auto-play="true">
      <div class="frame" v-for="img in imageList">
        <image class="image" resize="contain" :src="img.src"></image>
      </div>
    </slider>
  </div>
</template>

<style scoped>
  .image {
    width: 1080px;;
    height: 1920px;
  }
  .slider {
   width: 1080px;;
   height: 1920px;
  }
  .frame {
    width: 1080px;;
    height: 1920px;
    position: relative;
  }
</style>

<script>
  export default {
    data () {
      return {
        imageList: [
          { src: 'image/pic1.png'},
          { src: 'image/pic2.jpg'},
          { src: 'image/pic3.jpg'}
        ]
      }
    }
  }
</script>


